.container{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
}

.group{
    display: flex;
    align-items: center;
}

.label{
    margin-left: 7px;
    color: lightgray;
}

.label_done{
    color: black;
}

.bar{
    display: block;
    width: 5rem;
    height: 1px;
    background-color: lightgray;
    margin: auto 1rem;
}

.bar_done{
    background-color: black;
}
